<template>
    <h2>購物車</h2>
          <div class="container">
        <div class="mt-4">
          <!-- 產品Modal -->
          <table class="table align-middle">
            <thead>
              <tr>
                <th>圖片</th>
                <th>商品名稱</th>
                <th>價格</th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              <tr v-for = "product in products" :key="product.id">
                <td style="width: 200px">
                  <div :style = "{backgroundImage:`url(${product.imageUrl})`}"
                  style="height: 100px; background-size: cover; background-position: center"></div>
                </td>
                <td>
                  {{ product.title }}
                </td>
                <td>
                  <div class="h5">{{ product.price }} 元</div>
                  <del class="h6">原價 {{ product.origin_price }} 元</del>
                  <div class="h5">現在只要 {{ product.price }} 元</div>
                </td>
                <td>
                  <div class="btn-group btn-group-sm">
                    <button type="button" class="btn btn-outline-secondary"
                    @click = "openProductModal(product.id)"
                    :disabled = "isLoading === product.id"
                    >
                      <!-- <i v-if = "isLoading === product.id" class="fas fa-spinner fa-pulse"></i> -->
                      查看更多
                    </button>
                    <button type="button" class="btn btn-outline-danger"
                    @click = "addCart(product.id)"
                    :disabled = "isLoading === product.id"
                    >
                      <i v-if = "isLoading === product.id" class="fas fa-spinner fa-pulse"></i>
                      加到購物車
                    </button>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
          <!-- 購物車列表 -->
          <div class="text-end">
            <button class="btn btn-outline-danger" type="button"
            @click = "removeAllCart"
            :disabled = "cartData.carts.length === 0"
            >清空購物車</button>
          </div>
          <table class="table align-middle">
            <thead>
              <tr>
                <th></th>
                <th>品名</th>
                <th style="width: 150px">數量/單位</th>
                <th>單價</th>
              </tr>
            </thead>
            <tbody>
              <template v-if="cartData.carts">
                <tr v-for = "item in cartData.carts" :key ="item.id">
                  <td>
                    <button type="button" class="btn btn-outline-danger btn-sm"
                    @click="removeCart(item.id)"
                    >
                      <i v-if = "isLoading === item.id" class="fas fa-spinner fa-pulse"></i>
                      x
                    </button>
                  </td>
                  <td>
                    {{ item.product.title }}
                    <!-- <div class="text-success">
                      已套用優惠券
                    </div> -->
                  </td>
                  <td>
                    <div class="input-group input-group-sm">
                      <div class="input-group mb-3">
                        <input min="1" type="number" class="form-control"
                        v-model.number = "item.qty"
                        @change = "updateCart(item)"
                        >
                        <span class="input-group-text" id="basic-addon2">{{ item.product.unit }}</span>
                      </div>
                    </div>
                  </td>
                  <td class="text-end">
                    <!-- <small class="text-success">折扣價：</small> -->
                    {{ item.total }}
                  </td>
                </tr>
              </template>
            </tbody>
            <tfoot>
              <tr>
                <td colspan="3" class="text-end">總計</td>
                <td class="text-end">{{ cartData.total }}</td>
              </tr>
              <!-- <tr>
                <td colspan="3" class="text-end text-success">折扣價</td>
                <td class="text-end text-success">{{  }}</td>
              </tr> -->
            </tfoot>
          </table>
        </div>
      </div>
</template>

<script>
export default {
  data () {
    return {
      cartData: {
        carts: []
      },
      products: {},
      productId: '',
      isLoading: ''
    }
  },
  methods: {
    getProducts () {
      this.$http.get(`${process.env.VUE_APP_API}v2/api/${process.env.VUE_APP_PATH}/products/all`)
        .then((res) => {
          this.products = res.data.products
        })
    },
    getCart () {
      this.$http.get(`${process.env.VUE_APP_API}v2/api/${process.env.VUE_APP_PATH}/cart`)
        .then((res) => {
          this.cartData = res.data.data
        })
    },
    addCart (id, qty = 1) {
      const data = {
        product_id: id,
        qty
      }
      this.isLoading = id
      this.$http.post(`${process.env.VUE_APP_API}v2/api/${process.env.VUE_APP_PATH}/cart`, { data })
        .then(() => {
          this.getCart()
          this.isLoading = ''
          this.$refs.productModal.closeModal()
        })
    },
    removeCart (id) {
      this.isLoading = id
      this.$http.delete(`${process.env.VUE_APP_API}v2/api/${process.env.VUE_APP_PATH}/cart/${id}`)
        .then(() => {
          this.getCart()
          this.isLoading = ''
        })
    },
    removeAllCart () {
      this.$http.delete(`${process.env.VUE_APP_API}v2/api/${process.env.VUE_APP_PATH}/carts`)
        .then(() => {
          this.getCart()
        })
    },
    updateCart (item) {
      const data = {
        product_id: item.id,
        qty: item.qty
      }
      this.isLoading = item.id
      this.$http.put(`${process.env.VUE_APP_API}v2/api/${process.env.VUE_APP_PATH}/cart/${item.id}`, { data })
        .then((res) => {
          this.getCart()
          this.isLoading = ''
        })
    }
  },
  mounted () {
    this.getProducts()
    this.getCart()
  }
}
</script>
